// Special marker for cases where no sample is needed (from FACTOR_TABLE_V2)
const NO_SAMPLE_NEEDED_TEXT = 'Vzorek nen poteba';
const BATCH_SIZE = 5000;

// Progress tracking for chunk operations
let progressState = {
  isActive: false,
  cancelled: false,
  currentChunk: 0,
  totalChunks: 0,
  startTime: null,
  operation: ''
};

// Adaptive chunk sizing
let adaptiveChunkSize = BATCH_SIZE;
let chunkPerformanceHistory = [];

// Progress management functions
function showProgress(title, operation) {
  const container = document.getElementById('progress-container');
  const titleEl = document.getElementById('progress-title');
  const progressBar = document.getElementById('progress-bar');
  const progressText = document.getElementById('progress-text');
  const progressEta = document.getElementById('progress-eta');
  
  if (container && titleEl) {
    progressState.isActive = true;
    progressState.cancelled = false;
    progressState.currentChunk = 0;
    progressState.totalChunks = 0;
    progressState.startTime = Date.now();
    progressState.operation = operation;
    
    titleEl.textContent = title;
    progressBar.style.width = '0%';
    progressText.textContent = 'Pprava...';
    progressEta.textContent = '';
    container.style.display = 'block';
    
    // Wire cancel button
    const cancelBtn = document.getElementById('progress-cancel');
    if (cancelBtn) {
      cancelBtn.onclick = () => {
        progressState.cancelled = true;
        hideProgress();
        showNotification('Operace byla zruena.');
      };
    }
  }
}

function updateProgress(currentChunk, totalChunks, details = '') {
  if (!progressState.isActive) return;
  
  const progressBar = document.getElementById('progress-bar');
  const progressText = document.getElementById('progress-text');
  const progressEta = document.getElementById('progress-eta');
  
  progressState.currentChunk = currentChunk;
  progressState.totalChunks = totalChunks;
  
  const percentage = totalChunks > 0 ? (currentChunk / totalChunks) * 100 : 0;
  
  if (progressBar) progressBar.style.width = `${Math.min(percentage, 100)}%`;
  if (progressText) {
    const text = details || `Zpracovno ${currentChunk} z ${totalChunks} dvek`;
    progressText.textContent = text;
  }
  
  // Calculate ETA
  if (progressEta && currentChunk > 0 && totalChunks > 0) {
    const elapsed = Date.now() - progressState.startTime;
    const rate = currentChunk / elapsed; // chunks per ms
    const remaining = totalChunks - currentChunk;
    const eta = remaining / rate; // ms remaining
    
    if (eta > 1000 && eta < 300000) { // between 1s and 5min
      const seconds = Math.round(eta / 1000);
      progressEta.textContent = `Zbv ~${seconds}s`;
    }
  }
}

function hideProgress() {
  const container = document.getElementById('progress-container');
  if (container) container.style.display = 'none';
  progressState.isActive = false;
  progressState.cancelled = false;
}

function isProgressCancelled() {
  return progressState.cancelled;
}

// Adaptive chunk sizing functions
function recordChunkPerformance(chunkSize, processingTime, rowCount) {
  const rate = rowCount / processingTime; // rows per ms
  chunkPerformanceHistory.push({ chunkSize, rate, timestamp: Date.now() });
  
  // Keep only recent history (last 10 measurements)
  if (chunkPerformanceHistory.length > 10) {
    chunkPerformanceHistory = chunkPerformanceHistory.slice(-10);
  }
  
  // Update adaptive chunk size
  updateAdaptiveChunkSize();
}

function updateAdaptiveChunkSize() {
  if (chunkPerformanceHistory.length < 3) return;
  
  // Calculate average performance
  const avgRate = chunkPerformanceHistory.reduce((sum, h) => sum + h.rate, 0) / chunkPerformanceHistory.length;
  
  // Adjust chunk size based on performance
  // Target: ~100-500ms per chunk
  const targetTime = 300; // ms
  const targetRows = avgRate * targetTime;
  
  // Clamp to reasonable bounds
  adaptiveChunkSize = Math.max(1000, Math.min(10000, Math.round(targetRows)));
}

function getOptimalChunkSize(estimatedRows = null) {
  // For very large datasets (>100k rows), use smaller chunks to avoid memory issues
  if (estimatedRows && estimatedRows > 100000) {
    return Math.min(adaptiveChunkSize, 1000);
  }
  // For large datasets (>50k rows), use medium chunks
  if (estimatedRows && estimatedRows > 50000) {
    return Math.min(adaptiveChunkSize, 2000);
  }
  // For medium datasets (>10k rows), use adaptive size
  if (estimatedRows && estimatedRows > 10000) {
    return adaptiveChunkSize;
  }
  // For small datasets (<10k rows), use larger chunks for efficiency
  if (estimatedRows && estimatedRows < 10000) {
    return Math.max(adaptiveChunkSize, BATCH_SIZE);
  }
  // Default fallback
  return adaptiveChunkSize;
}

// Memory usage estimation and chunk size adjustment
function estimateMemoryUsage(chunkSize, columnCount) {
  // Rough estimation: each cell ~100 bytes, add overhead
  const estimatedBytes = chunkSize * columnCount * 100 * 1.5; // 50% overhead
  return estimatedBytes;
}

// Error handling and recovery for chunk operations
const chunkRetryConfig = {
  maxRetries: 3,
  retryDelay: 1000, // 1 second
  backoffMultiplier: 2
};

// Retry mechanism for failed chunk operations
async function retryChunkOperation(operation, attempt = 1) {
  try {
    return await operation();
  } catch (error) {
    if (attempt <= chunkRetryConfig.maxRetries && !isProgressCancelled()) {
      const delay = chunkRetryConfig.retryDelay * Math.pow(chunkRetryConfig.backoffMultiplier, attempt - 1);
      console.warn(`Chunk operation failed (attempt ${attempt}), retrying in ${delay}ms:`, error.message);
      
      // Update progress to show retry
      const progressText = document.getElementById('progress-text');
      if (progressText) {
        progressText.textContent = `Opakuji operaci (pokus ${attempt}/${chunkRetryConfig.maxRetries})...`;
      }
      
      await new Promise(resolve => setTimeout(resolve, delay));
      return retryChunkOperation(operation, attempt + 1);
    } else {
      throw error;
    }
  }
}

// Enhanced readRangeInBatches with retry capability
async function* readRangeInBatchesWithRetry(context, worksheet, range, batchSize = null, progressTitle = null) {
  const totalRows = range.rowCount;
  const startRow = range.rowIndex;
  const startCol = range.columnIndex;
  const colCount = range.columnCount;
  const effectiveBatchSize = batchSize || getMemoryOptimizedChunkSize(totalRows, colCount);
  const totalBatches = Math.ceil(totalRows / effectiveBatchSize);
  
  if (progressTitle && totalBatches > 1) {
    showProgress(progressTitle, 'read');
  }
  
  for (let offset = 0; offset < totalRows; offset += effectiveBatchSize) {
    if (isProgressCancelled()) {
      throw new Error('Operace byla zruena uivatelem');
    }
    
    const currentBatch = Math.floor(offset / effectiveBatchSize) + 1;
    
    const result = await retryChunkOperation(async () => {
      const startTime = Date.now();
      const rows = Math.min(effectiveBatchSize, totalRows - offset);
      const subRange = worksheet.getRangeByIndexes(startRow + offset, startCol, rows, colCount);
      subRange.load('values');
      await context.sync();
      
      const processingTime = Date.now() - startTime;
      recordChunkPerformance(effectiveBatchSize, processingTime, rows);
      
      return { values: subRange.values, startRow: startRow + offset, batchNumber: currentBatch, totalBatches };
    });
    
    if (progressTitle && totalBatches > 1) {
      updateProgress(currentBatch, totalBatches, `Natm dvku ${currentBatch}/${totalBatches}`);
    }
    
    yield result;
  }
  
  if (progressTitle && totalBatches > 1) {
    hideProgress();
  }
}

// Enhanced writeRangeInBatches with retry capability
async function writeRangeInBatchesWithRetry(context, worksheet, startRowIdx, startColIdx, rows, batchSize = null, progressTitle = null) {
  const effectiveBatchSize = batchSize || getMemoryOptimizedChunkSize(rows.length, rows[0]?.length || 1);
  const totalBatches = Math.ceil(rows.length / effectiveBatchSize);
  
  if (progressTitle && totalBatches > 1) {
    showProgress(progressTitle, 'write');
  }
  
  for (let offset = 0; offset < rows.length; offset += effectiveBatchSize) {
    if (isProgressCancelled()) {
      throw new Error('Operace byla zruena uivatelem');
    }
    
    const currentBatch = Math.floor(offset / effectiveBatchSize) + 1;
    
    await retryChunkOperation(async () => {
      const startTime = Date.now();
      const batchRows = rows.slice(offset, offset + effectiveBatchSize);
      const batchRowCount = batchRows.length;
      const writeRange = worksheet.getRangeByIndexes(startRowIdx + offset, startColIdx, batchRowCount, batchRows[0].length);
      writeRange.values = batchRows;
      await context.sync();
      
      const processingTime = Date.now() - startTime;
      recordChunkPerformance(effectiveBatchSize, processingTime, batchRowCount);
    });
    
    if (progressTitle && totalBatches > 1) {
      updateProgress(currentBatch, totalBatches, `Zapisuji dvku ${currentBatch}/${totalBatches}`);
    }
  }
  
  if (progressTitle && totalBatches > 1) {
    hideProgress();
  }
}

// Partial results recovery mechanism
let partialResults = {
  isRecoverable: false,
  operation: null,
  completedChunks: 0,
  totalChunks: 0,
  data: null
};

function savePartialProgress(operation, completedChunks, totalChunks, data = null) {
  partialResults = {
    isRecoverable: true,
    operation,
    completedChunks,
    totalChunks,
    data: data ? JSON.parse(JSON.stringify(data)) : null // deep clone
  };
}

function clearPartialProgress() {
  partialResults = {
    isRecoverable: false,
    operation: null,
    completedChunks: 0,
    totalChunks: 0,
    data: null
  };
}

function hasRecoverableProgress() {
  return partialResults.isRecoverable && partialResults.completedChunks > 0;
}

