// Handler for modal save button
function handleModalSave() {
  try {
    const modal = document.getElementById("reliability-modal");
    const engagementEl = document.getElementById('reliability-engagement-type');
    const controlTestingEl = document.getElementById('reliability-control-testing');
    const controlRiskEl = document.getElementById("reliability-control-risk");
    const inherentRiskEl = document.getElementById("reliability-inherent-risk");
    const rmmEl = document.getElementById('reliability-rmm-level');
    const analyticalTestsEl = document.getElementById("reliability-analytical-tests");
    

    // derive if empty
    const controlRisk = controlRiskEl && controlRiskEl.value ? controlRiskEl.value : (CONTROL_TESTING_TO_RISK[controlTestingEl?.value || ''] || reliabilityConfig.controlRisk);
    const rmmLevel = rmmEl && rmmEl.value ? rmmEl.value : resolveRmmLevel(controlRisk, inherentRiskEl?.value || reliabilityConfig.inherentRisk);

    const cfg = {
      engagementType: engagementEl?.value || reliabilityConfig.engagementType,
      controlTesting: controlTestingEl?.value || reliabilityConfig.controlTesting,
      controlRisk: controlRisk,
      inherentRisk: inherentRiskEl?.value || reliabilityConfig.inherentRisk,
      rmmLevel: rmmLevel,
      analyticalTests: analyticalTestsEl?.value || reliabilityConfig.analyticalTests,
      
    };

    // Validation: require main selections (engagementType, controlTesting, inherentRisk, analyticalTests)
    if (!cfg.engagementType || !cfg.controlTesting || !cfg.inherentRisk || !cfg.analyticalTests) {
      showNotification("Prosm vypl vechna povinn pole.");
      return;
    }

    // Persist
    reliabilityConfig = { ...reliabilityConfig, ...cfg };
    reliabilitySet = true;
    saveReliabilityToStorage();
    updateReliabilityDisplay();

    // Compute factor (prefer V2, fallback to legacy rows)
    const result = resolveFactorCompat(reliabilityConfig, fsParams);
    const el = document.getElementById('reliability-factor');
    const preview = document.getElementById('reliability-factor-preview');
    if (el) {
      if (result !== null) {
        el.placeholder = '';
        el.value = String(result);
        applyFactorStyle(el, String(result));
        if (preview) {
          preview.value = String(result);
          // mirror bold style for special text
          preview.style.fontWeight = (String(result) === NO_SAMPLE_NEEDED_TEXT) ? '700' : '';
        }
      } else {
        el.value = '';
        el.placeholder = '<Nevyplnno>';
        applyFactorStyle(el, null);
        if (preview) { preview.value = ''; preview.style.fontWeight = ''; }
      }
    }

    if (modal) modal.style.display = "none";
  } catch (err) {
    console.error("Chyba pi ukldn nastaven spolehlivosti:", err);
    showNotification("Dolo k neoekvan chyb pi ukldn.");
  }
}

// Persistence helpers
function saveReliabilityToStorage() {
  try {
    const payload = {
      engagementType: reliabilityConfig.engagementType,
      controlTesting: reliabilityConfig.controlTesting,
      controlRisk: reliabilityConfig.controlRisk,
      inherentRisk: reliabilityConfig.inherentRisk,
      analyticalTests: reliabilityConfig.analyticalTests,
      
      rmmLevel: reliabilityConfig.rmmLevel,
    };
    localStorage.setItem('reliabilityConfig', JSON.stringify(payload));
  } catch (e) {
    console.warn('Nelze uloit nastaven do localStorage:', e.message);
  }
}

function loadReliabilityFromStorage() {
  try {
    const raw = localStorage.getItem('reliabilityConfig');
    if (!raw) return;
    const obj = JSON.parse(raw);
    if (!obj) return;
    reliabilityConfig.engagementType = obj.engagementType || reliabilityConfig.engagementType;
    reliabilityConfig.controlTesting = obj.controlTesting || reliabilityConfig.controlTesting;
    reliabilityConfig.controlRisk = obj.controlRisk || reliabilityConfig.controlRisk;
    reliabilityConfig.inherentRisk = obj.inherentRisk || reliabilityConfig.inherentRisk;
    reliabilityConfig.rmmLevel = obj.rmmLevel || reliabilityConfig.rmmLevel;
    reliabilityConfig.analyticalTests = obj.analyticalTests || reliabilityConfig.analyticalTests;
    
    reliabilitySet = true;

    const result = resolveFactorCompat(reliabilityConfig, fsParams);
    const el = document.getElementById('reliability-factor');
    if (el) {
      if (result !== null) {
        el.placeholder = '';
        el.value = String(result);
        applyFactorStyle(el, String(result));
      } else {
        updateReliabilityDisplay();
      }
    }
  } catch (e) {
    console.warn('Nelze nast nastaven z localStorage:', e.message);
  }
}

function updateReliabilityDisplay() {
  const el = document.getElementById("reliability-factor");
  if (!reliabilitySet) {
    // Use placeholder so it appears with the same muted style as task 3's placeholder
    el.value = "";
    el.placeholder = "<Nevyplnno>";
    el.style.color = '';
    el.style.fontWeight = '';
    return;
  }
  const engagement = reliabilityConfig.engagementType || '-';
  const controlTesting = reliabilityConfig.controlTesting || '-';
  const control = reliabilityConfig.controlRisk || '-';
  const inherent = reliabilityConfig.inherentRisk || '-';
  const rmm = reliabilityConfig.rmmLevel || '-';
  const analytical = reliabilityConfig.analyticalTests || '-';
  const display = `innost: ${engagement}, Testy kontrol: ${controlTesting}, Kontroln riziko: ${control}, Pirozen riziko: ${inherent}, Hladina RMM: ${rmm}, Analytick testy: ${analytical}`;
  el.placeholder = "";
  el.value = display;
  el.style.color = '';
  el.style.fontWeight = '';
}

// Apply styling when factor is a specific error string
function applyFactorStyle(el, factor) {
  if (!el) return;
  if (factor === 'Test kontrol Error') {
    el.style.color = 'red';
    el.style.fontWeight = '700';
  } else if (factor === NO_SAMPLE_NEEDED_TEXT) {
    // Only emphasize with bold, keep default color
    el.style.color = '';
    el.style.fontWeight = '700';
  } else {
    el.style.color = '';
    el.style.fontWeight = '';
  }
}

