// Pevod 0-based indexu sloupce na psmenov oznaen (0 -> A)
function columnIndexToLetter(index) {
  let letter = '';
  let temp = index + 1;
  while (temp > 0) {
    let rem = (temp - 1) % 26;
    letter = String.fromCharCode(65 + rem) + letter;
    temp = Math.floor((temp - 1) / 26);
  }
  return letter;
}

// Enhanced main function for generating samples with improved chunk loading
async function generateSample() {
  try {
    const method = document.getElementById("sampling-method").value;
    const dataRange = document.getElementById("data-range").value;
    const valueColumn = document.getElementById("value-column").value;
    
    if (!dataRange) { showNotification('Prosm vyberte oblast dat.'); return; }
    // Require value column for TVS always; for StatisticalTVS only when method is MRW
    if ((usageMode === 'TVS' || (usageMode === 'StatisticalTVS' && method === 'monetary-random-walk')) && !valueColumn) {
      showNotification('Prosm vyberte i sloupec hodnot.');
      return;
    }
    // In ControlTest allow only RNG; in StatisticalTVS allow both methods
    if (usageMode === 'ControlTest' && method !== 'random-number-generator') {
      showNotification('V tomto reimu je povolen pouze Nhodn genertor sel.');
      return;
    }
    
    await Excel.run(async (context) => {
      const worksheet = context.workbook.worksheets.getActiveWorksheet();
      // tolerate addresses like 'Sheet1!$A$1:$F$100' by removing sheet and $
      let addr = dataRange;
      if (addr.indexOf('!') !== -1) addr = addr.split('!').pop();
      addr = addr.replace(/\$/g, '').trim();
      const range = worksheet.getRange(addr);
      
      range.load(['rowIndex', 'columnIndex', 'rowCount', 'columnCount']);
      await context.sync();

      let relColIndex = -1;
      if (usageMode === 'TVS' || (usageMode === 'StatisticalTVS' && method === 'monetary-random-walk')) {
        const absColIndex = getColumnIndex(String(valueColumn).toUpperCase());
        const rangeStartCol = typeof range.columnIndex === 'number' ? range.columnIndex : 0;
        relColIndex = absColIndex - rangeStartCol;
        if (relColIndex < 0 || relColIndex >= range.columnCount) {
          showNotification("Zadan sloupec nen v rmci vybranho rozsahu dat.");
          return;
        }
      }
      
      // Estimate if we need progress tracking
      const needsProgressBar = range.rowCount > getOptimalChunkSize();
      const progressTitle = needsProgressBar ? 'Natn dat pro vzorkovn' : null;
      
      let data = [];
      let headerRow = null;
      
      for await (const { values, batchNumber, totalBatches } of readRangeInBatches(context, worksheet, range, null, progressTitle)) {
        if (isProgressCancelled()) {
          showNotification('Generovn vzorku bylo zrueno.');
          return;
        }
        
        if (batchNumber === 1) {
          headerRow = values[0];
          data.push(headerRow); // Add header row once
          data = data.concat(values.slice(1)); // Add data rows
        } else {
          data = data.concat(values); // Add all rows (no header in subsequent batches)
        }
        
        // For datasets without progress bar, show simple notifications
        if (!progressTitle && totalBatches > 1) {
          showNotification(`Natm dvku ${batchNumber}/${totalBatches}...`);
        }
      }

      if (isProgressCancelled()) return;

      // Update progress for processing phase
      if (needsProgressBar) {
        showProgress('Generovn vzorku', 'process');
        updateProgress(1, 2, 'Zpracovvn dat...');
      }

      // Generovn vzorku podle zvolen metody
      let sample;
      if (method === "monetary-random-walk") {
        sample = generateMonetaryRandomWalk(data, relColIndex);
      } else {
        sample = generateRandomNumberSample(data, relColIndex);
      }
      
      if (needsProgressBar) {
        updateProgress(2, 2, 'Finalizace vsledk...');
      }
      
      // Zobrazen vsledk
      displayResults(sample, data.length);
      
      // Zvraznn vybranch dk
      await highlightSampleRows(context, worksheet, dataRange, sample);
      
      if (needsProgressBar) {
        hideProgress();
      }
      
      showNotification(`Vzorkovn dokoneno. Vybrno ${sample.length} poloek.`);
    });
  } catch (error) {
    hideProgress();
    console.error("Chyba pi generovn vzorku:", error);
    if (error.message && error.message.includes('zruena uivatelem')) {
      showNotification("Generovn vzorku bylo zrueno.");
    } else {
      showNotification("Chyba pi generovn vzorku: " + (error && error.message ? error.message : '')); 
    }
  }
}

// Pevod oznaen sloupce na index
function getColumnIndex(columnRef) {
  if (!isNaN(columnRef)) {
    return parseInt(columnRef) - 1;
  }
  
  let result = 0;
  for (let i = 0; i < columnRef.length; i++) {
    result = result * 26 + (columnRef.charCodeAt(i) - 'A'.charCodeAt(0) + 1);
  }
  return result - 1;
}
 // Enhanced helper to write rows in batches with progress tracking
 async function writeRangeInBatches(context, worksheet, startRowIdx, startColIdx, rows, batchSize = null, progressTitle = null) {
   const effectiveBatchSize = batchSize || getOptimalChunkSize(rows.length);
   const totalBatches = Math.ceil(rows.length / effectiveBatchSize);
   
   if (progressTitle && totalBatches > 1) {
     showProgress(progressTitle, 'write');
   }
   
   for (let offset = 0; offset < rows.length; offset += effectiveBatchSize) {
     if (isProgressCancelled()) {
       throw new Error('Operace byla zruena uivatelem');
     }
     
     const startTime = Date.now();
     const batchRows = rows.slice(offset, offset + effectiveBatchSize);
     const batchRowCount = batchRows.length;
     const writeRange = worksheet.getRangeByIndexes(startRowIdx + offset, startColIdx, batchRowCount, batchRows[0].length);
     writeRange.values = batchRows;
     await context.sync();
     
     const processingTime = Date.now() - startTime;
     recordChunkPerformance(effectiveBatchSize, processingTime, batchRowCount);
     
     const currentBatch = Math.floor(offset / effectiveBatchSize) + 1;
     if (progressTitle && totalBatches > 1) {
       updateProgress(currentBatch, totalBatches, `Zapisuji dvku ${currentBatch}/${totalBatches}`);
     }
   }
   
   if (progressTitle && totalBatches > 1) {
     hideProgress();
   }
 }

 // Enhanced helper to read rows in batches with progress tracking and cancellation
 async function* readRangeInBatches(context, worksheet, range, batchSize = null, progressTitle = null) {
   const totalRows = range.rowCount;
   const startRow = range.rowIndex;
   const startCol = range.columnIndex;
   const colCount = range.columnCount;
   const effectiveBatchSize = batchSize || getOptimalChunkSize(totalRows);
   const totalBatches = Math.ceil(totalRows / effectiveBatchSize);
   
   if (progressTitle && totalBatches > 1) {
     showProgress(progressTitle, 'read');
   }
   
   for (let offset = 0; offset < totalRows; offset += effectiveBatchSize) {
     if (isProgressCancelled()) {
       throw new Error('Operace byla zruena uivatelem');
     }
     
     const startTime = Date.now();
     const rows = Math.min(effectiveBatchSize, totalRows - offset);
     const subRange = worksheet.getRangeByIndexes(startRow + offset, startCol, rows, colCount);
     subRange.load('values');
     await context.sync();
     
     const processingTime = Date.now() - startTime;
     recordChunkPerformance(effectiveBatchSize, processingTime, rows);
     
     const currentBatch = Math.floor(offset / effectiveBatchSize) + 1;
     if (progressTitle && totalBatches > 1) {
       updateProgress(currentBatch, totalBatches, `Natm dvku ${currentBatch}/${totalBatches}`);
     }
     
     yield { values: subRange.values, startRow: startRow + offset, batchNumber: currentBatch, totalBatches };
   }
   
   if (progressTitle && totalBatches > 1) {
     hideProgress();
   }
 }


// Implementace nhodn penn prochzky
function generateMonetaryRandomWalk(data, columnIndex) {
  const totalValue = data.slice(1).reduce((sum, row) => {
    const raw = row[columnIndex];
    const parsed = parseNumberLoose(raw);
    const value = parsed !== null ? parsed : 0;
    return sum + Math.abs(value);
  }, 0);
  const sampleSize = calculateSampleSize(totalValue);
  if (!sampleSize || sampleSize < 1) return [];
  const interval = totalValue / sampleSize;
  const sample = [];
  let currentSum = 0;
  const rng = getTask6Rng();
  let nextTarget = (typeof rng === 'function' ? rng() : Math.random()) * interval;
  
  for (let i = 1; i < data.length; i++) {
  const parsed = parseNumberLoose(data[i][columnIndex]);
  const value = parsed !== null ? Math.abs(parsed) : 0;
    currentSum += value;
    
    if (currentSum >= nextTarget) {
      sample.push({
        rowIndex: i,
        rowData: data[i],
        value: data[i][columnIndex],
        cumulativeValue: currentSum
      });
      nextTarget += interval;
    }
  }
  
  return sample.slice(0, sampleSize);
}

// Implementace nhodnho genertoru sel
function generateRandomNumberSample(data, columnIndex) {
  const totalValue = data.slice(1).reduce((sum, row) => {
    const parsed = parseNumberLoose(row[columnIndex]);
    const value = parsed !== null ? parsed : 0;
    return sum + Math.abs(value);
  }, 0);
  const sampleSize = calculateSampleSize(totalValue);
  const dataRows = data.slice(1); // Peskoit hlaviku
  const sample = [];
  const usedIndices = new Set();
  
  while (sample.length < sampleSize && sample.length < dataRows.length) {
  const rng2 = getTask6Rng();
  const randomIndex = Math.floor((typeof rng2 === 'function' ? rng2() : Math.random()) * dataRows.length);
    
    if (!usedIndices.has(randomIndex)) {
      usedIndices.add(randomIndex);
      sample.push({
        rowIndex: randomIndex + 1, // +1 protoe peskakujeme hlaviku
        rowData: dataRows[randomIndex],
        value: dataRows[randomIndex][columnIndex]
      });
    }
  }
  
  return sample;
}

// Vpoet velikosti vzorku na zklad faktoru spolehlivosti
// Calculate sample size using TotalSum * factor / materiality when totalSum is provided.
// If totalSum is not provided or materiality/factor missing, falls back to the previous statistical method.
function calculateSampleSize(totalSum) {
  // Try to read factor from the reliability display first
  let factorNum = null;
  try {
    const factorEl = document.getElementById('reliability-factor');
    if (factorEl && factorEl.value) {
      if (factorEl.value === NO_SAMPLE_NEEDED_TEXT) {
        factorNum = 0;
      } else {
        const parsed = parseNumberLoose(factorEl.value);
        if (parsed !== null) factorNum = parsed;
      }
    }
  } catch (e) {}

  // If not numeric, try to resolve from table mapping
  if (factorNum === null || Number.isNaN(factorNum)) {
    const resolved = resolveFactorCompat(reliabilityConfig, fsParams);
    if (typeof resolved === 'number' && !Number.isNaN(resolved)) factorNum = resolved;
    else if (resolved === NO_SAMPLE_NEEDED_TEXT) factorNum = 0;
  }

  // Try to read materiality from stored significance config
  let materiality = null;
  try {
    const raw = localStorage.getItem('significanceConfig');
    if (raw) {
      const cfg = JSON.parse(raw);
      if (cfg && typeof cfg.rawValue === 'number') materiality = cfg.rawValue;
      // if rawValue stored as string, try to parse
      else if (cfg && cfg.rawValue) {
        const m = parseNumberLoose(cfg.rawValue);
        if (m !== null) materiality = m;
      }
    }
  } catch (e) {}

  // If totalSum provided and factor & materiality available, use the absolute-sum formula
  if (typeof totalSum === 'number' && !Number.isNaN(totalSum) && factorNum !== null && materiality && materiality > 0) {
    const raw = (totalSum * factorNum) / materiality;
    const n = Math.ceil(raw);
    return Math.max(0, n);
  }

  // Fallback: previous simple statistical method using reliabilityConfig
  try {
    const { confidence, expectedError, populationSize } = reliabilityConfig;
    const z = confidence === 95 ? 1.96 : confidence === 99 ? 2.58 : 1.645;
    const p = 0.5;
    const e = expectedError / 100;
    let n = (z * z * p * (1 - p)) / (e * e);
    if (populationSize > 0) {
      n = n / (1 + (n - 1) / populationSize);
    }
    return Math.ceil(n);
  } catch (e) {
    return 1;
  }
}

// Helper to compute absolute sum for a column from a 2D values array (skip header row)
function getTotalAbsoluteSum(values, columnIndex) {
  // Expect values to be a 2D array as returned by Excel range.values
  // Sum absolute values starting from second row (skip header)
  if (!Array.isArray(values) || values.length <= 1) return 0;
  return values.slice(1).reduce((sum, row) => {
    const raw = row && row.length > columnIndex ? row[columnIndex] : null;
    const parsed = parseNumberLoose(raw);
    if (parsed === null) return sum;
    return sum + Math.abs(parsed);
  }, 0);
}

// Robust number parser: accepts '1 234,56', '1234.56', '1,234.56' and returns Number or null
function parseNumberLoose(input) {
  if (input === null || input === undefined) return null;
  let s = String(input).trim();
  if (s === '') return null;
  // remove non-breaking spaces and regular spaces
  s = s.replace(/\s+/g, '');
  // If contains comma but no dot, assume comma is decimal separator
  if (s.indexOf(',') !== -1 && s.indexOf('.') === -1) {
    s = s.replace(',', '.');
  } else {
    // remove commas used as thousand separators
    s = s.replace(/,/g, '');
  }
  // remove any other non-digit/.- characters
  s = s.replace(/[^0-9.\-]/g, '');
  const n = Number(s);
  return Number.isFinite(n) ? n : null;
}

