// Helper to safely get value from an element
function _get_value(id) {
  const el = document.getElementById(id);
  if (el) {
    if (el.tagName === 'SELECT') {
      try { return el.options[el.selectedIndex].text; } catch (e) { return ''; }
    }
    return el.value || '';
  }
  return '';
}

function _get_selected_value(id) {
  const el = document.getElementById(id);
  return el ? el.value : '';
}

// Formats parameters into a 2D array for writing to the worksheet
function _format_params(params) {
  const out = [];
  out.push(['Parametry Vzorkovie', '']);
  for (const key in params) {
    if (Object.hasOwnProperty.call(params, key)) {
      const value = params[key];
      out.push([key, value === null || value === undefined ? '' : value]);
    }
  }
  return out;
}

// Prints parameters to the specified location
async function printParameters(params, target) {
  try {
    let success = false;
    await Excel.run(async (context) => {
      const formatted = _format_params(params);
      let authorRowIdx = formatted.findIndex(row => row[0] && row[0].toLowerCase().includes('autor vzorku'));
      if (target === 'new-sheet') {
        const newSheet = context.workbook.worksheets.add("ParametryVzorku");
        newSheet.activate();
        const range = newSheet.getRange('A1');
        range.worksheet.getRangeByIndexes(0, 0, formatted.length, 2).values = formatted;
        if (authorRowIdx > -1) {
          const authorCell = newSheet.getCell(authorRowIdx, 1);
          authorCell.format.fill.color = '#fff200';
          authorCell.format.font.bold = true;
        }
        success = true;
      } else if (target === 'above') {
        const dataRange = document.getElementById('data-range').value;
        if (!dataRange) {
          showNotification('Oblast dat nen vybrna. Parametry budou vloeny na nov list.');
          const newSheet = context.workbook.worksheets.add();
          newSheet.activate();
          const range = newSheet.getRange('A1');
          range.worksheet.getRangeByIndexes(0, 0, formatted.length, 2).values = formatted;
          if (authorRowIdx > -1) {
            const authorCell = newSheet.getCell(authorRowIdx, 1);
            authorCell.format.fill.color = '#fff200';
            authorCell.format.font.bold = true;
          }
          success = true;
          return;
        }
        let addr = dataRange;
        if (addr.indexOf('!') !== -1) addr = addr.split('!').pop();
        addr = addr.replace(/\$/g, '').trim();
        let range, startRow, sheet, columnCount;
        try {
          range = context.workbook.worksheets.getActiveWorksheet().getRange(addr);
          range.load(['rowIndex']);
          await context.sync();
          startRow = range.rowIndex;
          sheet = context.workbook.worksheets.getActiveWorksheet();
          const usedRange = sheet.getUsedRange();
          usedRange.load('columnCount');
          await context.sync();
          columnCount = usedRange.columnCount;
        } catch (e) {
          showNotification('Oblast dat nen platn nebo nen aktivn list. Parametry nebyly vloeny.');
          return;
        }
        if (typeof startRow !== 'number' || !sheet || !columnCount) {
          showNotification('Oblast dat nen platn nebo nen aktivn list. Parametry nebyly vloeny.');
          return;
        }
        sheet.getRangeByIndexes(startRow, 0, formatted.length, columnCount).insert(Excel.InsertShiftDirection.down);
        sheet.getRangeByIndexes(startRow, 0, formatted.length, 2).values = formatted;
        if (authorRowIdx > -1) {
          const authorCell = sheet.getCell(startRow + authorRowIdx, 1);
          authorCell.format.fill.color = '#fff200';
          authorCell.format.font.bold = true;
        }
        success = true;
      }
      await context.sync();
    });
    return success;
  } catch (e) {
    console.error('Chyba pi tisku parametr:', e);
    showNotification('Chyba pi tisku parametr.');
    return false;
  }
}

// Prints parameters to a selected cell (new Provovi-style method)
async function printParametersToCell(params, cellInfo) {
  if (!cellInfo) {
    showNotification('Nejprve vyberte buku pro tisk parametr.');
    return false;
  }
  
  try {
    let success = false;
    await Excel.run(async (context) => {
      const sheet = context.workbook.worksheets.getActiveWorksheet();
      const formatted = _format_params(params);
      
      // Write parameters starting from the selected cell
      const startRow = cellInfo.rowIndex;
      const startCol = cellInfo.columnIndex;
      const targetRange = sheet.getRangeByIndexes(startRow, startCol, formatted.length, 2);
      targetRange.values = formatted;
      
      // Highlight author row
      let authorRowIdx = formatted.findIndex(row => row[0] && row[0].toLowerCase().includes('autor vzorku'));
      if (authorRowIdx > -1) {
        const authorCell = sheet.getCell(startRow + authorRowIdx, startCol + 1);
        authorCell.format.fill.color = '#fff200';
        authorCell.format.font.bold = true;
      }
      
      await context.sync();
      success = true;
    });
    return success;
  } catch (e) {
    console.error('Chyba pi tisku parametr do buky:', e);
    showNotification('Chyba pi tisku parametr: ' + e.message);
    return false;
  }
}

// Gathers all sampler parameters from UI and storage
function gatherSamplerParameters() {
  const params = {};
  // Usage mode
  params['el generovn vzorku'] = (function(){
    const sel = document.getElementById('usage-mode');
    if (!sel) return usageMode;
    const opt = sel.options[sel.selectedIndex];
    return opt ? opt.text : usageMode;
  })();
  if (usageMode === 'StatisticalTVS') {
    params['Dvod pro pedvolbu'] = usageModeReason || _get_value('usage-mode-reason');
  }
  // Task 2
  if (usageMode === 'TVS') {
    params['innost (typ zakzky)'] = reliabilityConfig.engagementType || '';
    params['Testy kontrol (proveden)'] = reliabilityConfig.controlTesting || '';
    params['Kontroln riziko'] = reliabilityConfig.controlRisk || '';
    params['Pirozen riziko'] = reliabilityConfig.inherentRisk || '';
    params['Hladina RMM'] = reliabilityConfig.rmmLevel || '';
    params['Provdm analytick testy?'] = reliabilityConfig.analyticalTests || '';
    
    params['Faktor spolehlivosti (vsledn)'] = _get_value('reliability-factor');
  } else {
    params['innost (typ zakzky)'] = 'N/A (mimo TVS)';
    params['Testy kontrol (proveden)'] = 'N/A (mimo TVS)';
    params['Kontroln riziko'] = 'N/A (mimo TVS)';
    params['Pirozen riziko'] = 'N/A (mimo TVS)';
    params['Hladina RMM'] = 'N/A (mimo TVS)';
    params['Provdm analytick testy?'] = 'N/A (mimo TVS)';
    params['Faktor spolehlivosti (vsledn)'] = 'N/A (mimo TVS)';
  }
  // Task 3
  if (usageMode === 'TVS') {
    params['Typ vznamnosti'] = _get_value('significance-type-display');
    params['Hodnota vznamnosti'] = _get_value('significance-value-display');
    params['Zdvodnn vznamnosti'] = _get_value('significance-justification-display');
  } else {
    params['Typ vznamnosti'] = 'N/A (mimo TVS)';
    params['Hodnota vznamnosti'] = 'N/A (mimo TVS)';
    params['Zdvodnn vznamnosti'] = 'N/A (mimo TVS)';
  }
  // Task 4
  params['Oblast dat'] = _get_value('data-range');
  params['Sloupec pro vbr'] = usageMode === 'TVS' ? _get_value('value-column') : 'Nepovinn (mimo TVS)';
  // Task 5
  if (usageMode === 'TVS') {
    params['Celkov suma absolutnch hodnot'] = _get_value('task5-result-display');
    params['Vypoten velikost vzorku'] = _get_value('task5-size-display');
    const useCalculated = _get_value('task5-use-calculated');
    params['Pout vypotenou velikost'] = useCalculated;
    if (useCalculated === 'Ne') {
      params['Uivatelsk velikost vzorku'] = _get_value('task5-override-size');
      params['Dvod uivatelsk velikosti'] = _get_value('task5-override-reason');
    }
  } else {
    params['Celkov suma absolutnch hodnot'] = 'N/A (mimo TVS)';
    params['Vypoten velikost vzorku'] = 'N/A (mimo TVS)';
    params['Pout vypotenou velikost'] = 'N/A (mimo TVS)';
    if (usageMode === 'ControlTest') {
      const preset = _get_selected_value('task5-ct-preset');
      params['Pedvolba velikosti pro test kontrol'] = preset ? (preset === 'Jine' ? 'Jin' : preset) : '';
    }
    params['Uivatelsk velikost vzorku'] = _get_value('task5-override-size');
    params['Dvod uivatelsk velikosti'] = _get_value('task5-override-reason');
  }
  // Task 6
  params['Metoda vbru vzorku'] = _get_value('task6-method-display');
  const finalSizeTxt = _get_value('task6-final-size-display');
  params['Finln velikost vzorku'] = finalSizeTxt;
  if (usageMode === 'TVS') {
    params['Vylouit nadlimitn poloky'] = _get_value('task6-exclude-over-significance');
    params['Suma pro vpoet (po prav)'] = _get_value('task6-param-sum-display');
    params['Poet poloek nad vznamnost'] = _get_value('task6-significant-count-display');
    // Note when n=0
    const n0 = (() => { const v = parseNumberLoose(finalSizeTxt); return v === 0; })();
    if (n0) {
      params['Poznmka k velikosti vzorku'] = 'Vypoten/finln velikost n=0  vzorek nen poteba nebo nedostaten suma';
    }
  } else {
    params['Vylouit nadlimitn poloky'] = 'N/A (mimo TVS)';
    params['Suma pro vpoet (po prav)'] = 'N/A (mimo TVS)';
    params['Poet poloek nad vznamnost'] = 'N/A (mimo TVS)';
  }
  params['Seed pro genertor'] = _get_value('task6-seed');
  // Add new parameters
  params['Autor vzorku'] = "Neznm uivatel";
  params['Datum a as vyhotoven'] = new Date().toLocaleString();

  return params;
}
